import uniTag from "@/components/uni-ui/uni-tag/uni-tag.vue"
export default {
	props:['list'],
	components: {
		uniTag
	},
	data() {
		return {

		};
	},
	methods: {
		isUnknowTime(date){
			const flag = this.$common.isUnknowTime(date)
			if(flag) return "待定"
			return date
		},
		navigator(id){
			this.$emit('click',id)
		},
		type(i) {
			if(i == 0)
				return 'success'
			else if(i == 1)
				return 'warning'
			else 
				return 'primary'
		},
		isEnd(e){
			return this.$api.isEnd(e)
		},
		getGameType(i){
			return this.$api.getGameType(i)
		},
	}
}
